/* 스킵 메뉴 */
#skip {
    position: relative;
    z-index: 100000;
}

#skip a {
    padding: 20px 40px;
    position: absolute;
    left: -400px;
    top: 5px;
    background: #000;
    color: #fff;
}

#skip a:active,
#skip a:focus {
    left: 5px;
}

/* 컨테이너 */
.container {
    padding: 0 20px;
    margin: 0 auto;
    max-width: 1160px;
    box-sizing: border-box;
}

/* 섹션 공통요소 */
.section {
    padding: 120px 0;
}

.section>h2 {
    font-size: 50px;
    line-height: 1;
    text-align: center;
    margin-bottom: 20px;
}

.section>p {
    font-size: 22px;
    font-weight: 300;
    color: #666;
    text-align: center;
    margin-bottom: 70px;
    padding: 0 20px;
    word-break: keep-all;
    line-height: 1.5;
}

.section>span {
    display: block;
    text-align: center;
    font-size: 16px;
    text-decoration: underline;
    text-underline-position: under;
    margin-bottom: 20px;
}

@media(max-width:960px){
    .section > h2 {
        font-size: 40px;
    }
    
    .section>p {
        font-size: 18px;
        margin-bottom: 60px;
    }
}

@media(max-width:600px){
    .section > h2 {
        font-size: 30px;
    }

    .section>p {
        font-size: 16px;
        margin-bottom: 50px;
    }
}

/* 폰트 */
.nexon {
    font-family: 'NexonLv1Gothic';
    font-weight: 400;
}

/* 배경 */
.gray {
    background-color: #f5f5f5;
}

/* fixed */
body{
    overflow: overlay;
}
body.fixed {
    height: 100vh;
    overflow: hidden;
}

/* 탑 버튼 */
#top__btn {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: -220px;
    width: 40px;
    height: 40px;
    line-height: 40px;
    background-color: rgba(0, 0, 0, 0.4);
    padding: 10px;
    color: #fff;
    text-align: center;
    border-radius: 50%;
    z-index: 10000;
    cursor: pointer;
    transition: bottom .4s ease;
}

/* 가로스크롤 방지 */
#main {
    overflow-x: hidden;
}

#top__btn.show {
    bottom: 20px;
}

#top__btn:hover {
    background: #fff;
    color: #000;
}

